.signin-container {
    width: 100%;
    max-width: 400px;
    padding: 10px 40px;
    border: 1px solid #e1e1e1;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(214, 154, 154, 0.1);
    background-color: #fff;

    .signin-container h2 {
        font-size: 24px;
        margin-bottom: 20px;
        text-align: center;
        color: #333;
    }

    .form-group {
        margin-bottom: 20px;
    }

    label {
        display: block;
        font-size: 16px;
        margin-bottom: 8px;
        color: #555;
    }

    input[type="text"],
    input[type="email"],
    input[type="password"] {
        width: 100%;
        padding: 10px 10px;
        border: 1px solid #d1d1d1;
        border-radius: 5px;
        font-size: 16px;
        color: #333;
    }

    .error {
        border-color: red;
    }

    .error-message {
        color: red;
        font-size: 14px;
        margin-top: 5px;
    }

    .signInSubmit {
        display: block;
        width: 100%;
        padding: 12px;
        background-color: #007bff;
        color: #fff;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        font-size: 18px;
        transition: background-color 0.3s;
    }

    button:disabled {
        background-color: #ccc;
        cursor: not-allowed;
    }

    button:hover {
        background-color: #0056b3;
    }
}